import React, { useEffect, useState } from 'react'
import { VictoryBar, VictoryChart, VictoryAxis, VictoryTheme, VictoryLabel, VictoryZoomContainer } from 'victory'
import './Chart.css'

interface DataItem {
    city: string,
    donations: number
}
interface Props {
    data: DataItem[],
    setSearch: (c: string) => void,
    loading:boolean
}

const CityChart = ({ data, setSearch, loading }: Props) => {
    
    let citiesData = [
        { city: 'Delhi NCR', donations: 0,label:0 },
        { city: 'Agra', donations: 0 , label:0},
        { city: 'Lucknow', donations: 0, label:0 },
        { city: 'Jaipur', donations: 0, label:0 },
        { city: 'Kolkata', donations: 0, label:0 },
        { city: 'Ranchi', donations: 0, label:0 },
        { city: 'Odisha', donations: 0, label:0 },
        { city: 'Varanasi', donations: 0, label:0 },
        { city: 'Pune', donations: 0, label:0 },
        { city: 'Kanpur', donations: 0, label:0 },
        { city: 'Consulting', donations: 0, label:0 }
      ]
    const [d , setData ] = useState(citiesData)
    useEffect(() => {
        console.log(' data changed ', data)
        if(!loading && data) {
            if(data.length > 0) {
                setData(d => {
                    return d.map(da => {
                        let i = data.findIndex(c => c.city === da.city)
                        if(i < 0 )
                            return da
                        da.donations = data[i].donations
                        da.label = data[i].donations
                        return da
                    })
                })
            }
        }
        
    },[data])

    return (
        <div className="container chart-container my-4">
            <VictoryChart
                domainPadding={50}
                theme={VictoryTheme.material}
                width={800}
                height={400}
                containerComponent={ <VictoryZoomContainer allowZoom={false} />}
            >
                <VictoryLabel text="City Report" x={225} y={30} textAnchor="middle" />
                <VictoryAxis

                    tickValues={data.map(f => f.city)}
                    tickFormat={x => x}
                />
                <VictoryAxis
                    dependentAxis
                    tickFormat={x => x}
                />
                <VictoryBar
                    data={d}
                    x="city"
                    y="donations"
                    events={[{
                        target: "data",
                        eventHandlers: {
                            onClick: () => {
                                return [{
                                    target: "data",
                                    mutation: (props) => {
                                        console.log(props.data[props.index].city)
                                        setSearch(props.data[props.index].city)
                                        return true
                                    }
                                }];
                            }
                        }
                    }]}
                />
            </VictoryChart>
        </div>
    )
}

export default CityChart
